@font-face {
  font-family: 'KorailRoundGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2212@1.0/KorailRoundGothicMedium.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

:root {
  --box-margin: 40px;
  --data-size: 80%;
  --text-color: black;
  --text-size: 18px;
  --menu-femily: 'TheJamsil5Bold';
  --list-femily: 'KorailRoundGothicMedium';
}

/* ---------------------Bisic.css--------------------- */
.dash_container {
  width: 100%;
  height: 100%;
  float: left;
  margin-top: 3px;
}
.dash_box {
  width: 80%;
  height: 100%;
  float: left;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
}
/* ---------------------TotalInfo.css--------------------- */
.total_container {
  width: 100%;
  height: 100%;
  float: left;
  margin-top: 3px;
  
}
.total_box {
  width: var(--data-size);
  height: auto;
  float: left;
  margin-left: 30px;
  margin-top: var(--box-margin);
  background-color: white;
}
/* ---------------------CompanyInfo.css--------------------- */
.company_container {
  width: 100%;
  height: 100%;
  float: left;
  margin-top: 3px;
  
}
.company_box {
  width: var(--data-size);
  height: auto;
  float: left;
  margin-left: 30px;
  margin-top: var(--box-margin);
  background-color: white;
}
/* ---------------------------공용소스--------------------------- */
.test1 {
  scale: 1.0;
  margin-top: 20px;
}
.test2 {
  scale: 1.0;
  margin-top: 20px;
}
.fileInput {
  display: none;
}
.centerCell {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.admin_defaultRow {
  background-color: #fff3d8;
}
.errorRow .MuiDataGrid-cell {
  background-color: #e76262;
  color: white;
}
.editingRow .MuiDataGrid-cell {
  background-color: #aeff19; 
}
.next_page {
  float: right;
  text-decoration: none;
  color: black;
  margin-right: 40px;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  margin-top: 20px;
}
.befor_page {
  float: right;
  text-decoration: none;
  color: black;
  margin-right: 40px;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  margin-top: 20px;
}
.next_page1 {
  text-decoration: none;
  color: black;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  padding: 0;
  margin: 0;
}
.title_text {
  font-family: var(--menu-femily);
  font-size: 30px;
  margin-top: 30px;
}
.nav_sub_box {
  width: 100vw;
  height: 80px;
  background-color: #ffffff;
  box-shadow:
  0 2px 2px rgba(0,0,0,0.11), 
  0 6px 8px rgba(0,0,0,0.11);
}
